import React from 'react'
import { Helmet } from 'react-helmet'
import { BiSad, BiMoon } from 'react-icons/bi'

const Errore = () => {
	return (
		<div className="error-page" style={{
			minHeight: '100vh',
			background: 'linear-gradient(rgba(0, 0, 0, 0.85), rgba(0, 0, 0, 0.85)), url(https://images.unsplash.com/photo-1511497584788-876760111969?ixlib=rb-1.2.1&auto=format&fit=crop&w=1920&q=80)',
			backgroundSize: 'cover',
			backgroundAttachment: 'fixed',
			color: '#f8f9fa'
		}}>
			<Helmet>
				<title>Errore 404</title>
				<meta name="description" content="La pagina cercata, simile alla luna leopardiana, sembra esser svanita nel notturno..." />
				<meta name="theme-color" content="#2c3e50" />
				<meta httpEquiv="X-Content-Type-Options" content="nosniff" />
				<meta httpEquiv="X-Frame-Options" content="DENY" />
				<meta httpEquiv="X-XSS-Protection" content="1; mode=block" />
			</Helmet>

			<div className="container" style={{
				paddingTop: '1rem',
				paddingBottom: '0.2rem'
			}}>
				<div className="parchment-error text-center mx-auto p-5 rounded-3" style={{
					maxWidth: '700px',
					background: 'rgba(44, 62, 80, 0.9)',
					border: '2px solid #8b7355',
					boxShadow: '0 0 20px rgba(139, 115, 85, 0.3)'
				}}>
					<div className="error-icon mb-4" style={{
						fontSize: '5rem',
						color: '#8b7355',
						filter: 'drop-shadow(0 0 8px rgba(139, 115, 85, 0.5))'
					}}>
						<BiMoon className="spin" style={{ animation: 'spin 8s linear infinite' }} />
					</div>
					
					<h1 className="display-3 font-playfair mb-3" style={{
						textShadow: '2px 2px 4px rgba(0,0,0,0.3)'
					}}>
						404
					</h1>
					
					<h2 className="h3 mb-4" style={{
						fontFamily: "'Crimson Text', serif",
						letterSpacing: '0.5px'
					}}>
						<BiSad className="me-2" />
						Pagina Svanita nel Notturno
					</h2>

					<div className="quote-box mb-4 mx-auto" style={{
						borderLeft: '3px solid #8b7355',
						padding: '1rem 2rem',
						maxWidth: '600px',
						background: 'rgba(255, 250, 240, 0.05)'
					}}>
						<blockquote className="fst-italic text-light">
							"Che fai tu, pagina, in ciel? Dimmi, che fai,<br />
							Silenziosa pagina?"
						</blockquote>
					</div>

					<p className="lead mb-4" style={{ fontSize: '1.25rem' }}>
						Come il pastore errante, cerchi invano<br />
						La via tra i sentieri del sapere...
					</p>

					<a
						href="/" 
						className="btn btn-outline-warning px-5 py-2 rounded-pill"
						style={{
							borderWidth: '2px',
							transition: 'all 0.3s ease',
							fontFamily: "'Playfair Display', serif"
						}}
					>
						Torna all'Limo Scialbo
					</a>
				</div>
			</div>

			<style>
				{`
					@keyframes spin {
						100% { transform: rotate(360deg) }
					}

					.spin {
						animation: spin 8s linear infinite
					}

					.btn-outline-warning:hover {
						background-color: #8b7355;
						border-color: #8b7355;
						color: #fff !important;
						transform: translateY(-2px)
					}

					.parchment-error {
						position: relative;
						overflow: hidden
					}

					.parchment-error::before {
						content: '';
						position: absolute;
						top: -50%;
						left: -50%;
						width: 200%;
						height: 200%;
						background: linear-gradient(45deg, 
							transparent 45%, 
							rgba(139, 115, 85, 0.1) 50%, 
							transparent 55%);
						animation: shine 6s infinite
					}

					@keyframes shine {
						0% { transform: rotate(45deg) translate(-50%, -50%) }
						100% { transform: rotate(45deg) translate(50%, 50%) }
					}
				`}
			</style>
		</div>
	)
}

export default Errore